/* slide */
.main_slide {
   width: 1000px;
   height: auto;
   position: relative;
   margin: 0 auto;
   background-color: #ccc;
   /* overflow: hidden; */
}

/* slide content */
.slide_view {
   width: 1000px;
   height: 600px;
   overflow: hidden;
   position: relative;
}
.slide_wrap {
   width: auto;
   height: auto;
   left: 0;
   position: absolute;
   display: flex;
   justify-content: flex-start;
   flex-wrap: nowrap;
}
.slide_item {
   position: relative;
   width: 1000px;
   height: 600px;
   text-align: center;
   line-height: 600px;
   -webkit-transform: translate(0, 0) scale(1);
           transform: translate(0, 0) scale(1);
   opacity: 1;
   /* transition: 0.5s ease-in-out; */
   font-size: 30px;
}

/* slide_item on */
.slide_item.on {
   opacity: 1;
   -webkit-transform: translate(0, 0) scale(1);
           transform: translate(0, 0) scale(1);
   z-index: 5;
}

/* 데모를 위한 style */
.item0 {
   background-color: turquoise;
}
.item1 {
   background-color: wheat;
}
.item2 {
   background-color: gold;
}
.item3 {
   background-color: coral;
}
.item4 {
   background-color: skyblue;
}
.last {
   position: absolute;
   left: 0;
   top: 50%;
   -webkit-transform: translate(-100%, -50%);
           transform: translate(-100%, -50%);
}
.first {
   position: absolute;
   right: 0;
   top: 50%;
   -webkit-transform: translate(100%, -50%);
           transform: translate(100%, -50%);
}

/* slide dot */
.slide_dots {
   position: absolute;
   left: 50%;
   bottom: 0;
   display: flex;
   justify-content: center;
   -webkit-transform: translate(-50%, 0);
           transform: translate(-50%, 0);
   z-index: 10;
}
.slide_dot {
   background-color: palegoldenrod;
   margin: 0 20px;
   width: 20px;
   height: 20px;
   border-radius: 10px;
   text-align: center;
   line-height: 20px;
   z-index: 10;
}
.slide_dot.on {
   background-color: red;
}
.slide_dot a {
   color: #000;
}
/* slide arrows */
.slide_arrows {
   padding: 10px;
}

/* slide auto play/pause button */
.btn_play {
   padding: 10px;
}
.btn_play.pause {
   background-color: red;
   color: #fff;
}
/* http://meyerweb.com/eric/tools/css/reset/ */
/* v1.0 | 20080212 */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-size: 100%;
	vertical-align: top;
   background: transparent;
   font-weight:normal;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

/* remember to define focus styles! */
:focus {
	outline: 0;
}

/* remember to highlight inserts somehow! */
ins {
	text-decoration: none;
}
del {
	text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a {
   text-decoration:none;
}
